<template>
  <div class="index" :style="{height: bodyHeight + 'px'}">
    <div class="time">
      <div class="day">{{day}}天</div>
      <div class="HMS">
        {{hour}} : {{minute}} : {{second}}
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment'
export default {
  data () {
    return {
      bodyHeight: '',
      countDownTime: null,
      day: null,
      hour: null,
      minute: null,
      second: null
    }
  },
  created () {
    this.bodyHeight = document.body.clientHeight
    setInterval(() => {
      this.getTime()
    }, 1000)
  },
  methods: {
    toContent () {
      this.$router.push('/content')
    },
    getTime (time) {
      let target = moment('2019-12-22 08:30:30')
      let now = moment().format('YYYY-MM-DD HH:mm:ss')
      let diffCount = target.diff(now, 'second')
      this.day = parseInt(diffCount / 60 / 60 / 24)
      this.hour = parseInt((diffCount - this.day * 24 * 60 * 60) / 60 / 60) < 10 ? '0' + parseInt((diffCount - this.day * 24 * 60 * 60) / 60 / 60) : parseInt((diffCount - this.day * 24 * 60 * 60) / 60 / 60)
      this.minute = parseInt((diffCount - this.day * 24 * 60 * 60 - this.hour * 60 * 60) / 60) < 10 ? '0' + parseInt((diffCount - this.day * 24 * 60 * 60 - this.hour * 60 * 60) / 60) : parseInt((diffCount - this.day * 24 * 60 * 60 - this.hour * 60 * 60) / 60)
      this.second = (diffCount - this.day * 24 * 60 * 60 - this.hour * 60 * 60 - this.minute * 60) < 10 ? '0' + (diffCount - this.day * 24 * 60 * 60 - this.hour * 60 * 60 - this.minute * 60) : (diffCount - this.day * 24 * 60 * 60 - this.hour * 60 * 60 - this.minute * 60)
    }
  },
  mounted () {
    var _this = this
    window.onresize = function () {
      _this.bodyHeight = document.body.clientHeight
    }
  }
}
</script>

<style lang="scss" scoped>
#app {
  height: 100%;
}
.index {
  background-color: #fff;
  display: flex;
  justify-content: center;
  margin-top: 50%;
  .time {
    .day {
      text-align: center;
      font-size: 30px;
    }
    .HMS {
      margin-top: 10px;
    }
  }
}
</style>



